import React, { useState } from "react";
import styles from "./Styles";
import { GrReactjs, GrNode } from "react-icons/gr";
import { IoLogoJavascript } from "react-icons/io5";

import { IoLogoCss3 } from "react-icons/io";

const Menu = ({ open, setOpen, currentItem, setCurrentItem }) => {
  const { Main } = styles;
  const [all, setAll] = useState(true);
  const [components, setComponents] = useState(true);
  const [source, setSource] = useState(true);

  const arrowGeneral = all
    ? "fas fa-chevron-down flecha"
    : "fas fa-chevron-right flecha";
  const arrowComponents = components
    ? "fas fa-chevron-down flecha"
    : "fas fa-chevron-right flecha";
  const arrowSource = source
    ? "fas fa-chevron-down flecha"
    : "fas fa-chevron-right flecha";

  const addOpen = (name) => {
    const exists = open.find((ele) => ele === name);
    !exists && setOpen([...open, name]);
    setCurrentItem(name);
  };

  return (
    <Main>
      <div className="item activo first-item" onClick={() => setAll(!all)}>
        <div>
          <i className={arrowGeneral}></i>
          <small>Portfolio</small>
        </div>

        <div className="iconos-dere">
          <i className="fas fa-file-medical dericon d1"></i>
          <i className="fas fa-folder-plus dericon "></i>
          <i className="fas fa-redo dericon"></i>
          <i className="fas fa-minus-square dericon"></i>
        </div>
      </div>

      {all && (
        <section>
          <div className="item">
            <i className="fas fa-chevron-right flecha"></i>
            <i className="fas fa-folder carpeta"></i>
            <small>node_modules</small>
            <i className="fas fa-cog icono"></i>
          </div>
          <div className="item">
            <i className="fas fa-chevron-right flecha"></i>
            <i className="fas fa-folder carpeta carpeta2"></i>
            <small>public</small>
            <i className="fas fa-users icono icono2"></i>
          </div>

          <div
            className="item"
            onClick={() => {
              setSource(!source);
              setComponents(false);
            }}
          >
            <i className={arrowSource}></i>
            <i
              className={
                source
                  ? "fas fa-folder-open carpeta carpeta3"
                  : "fas fa-folder carpeta carpeta3"
              }
            ></i>
            <small>src</small>
            {!source && <i className="fas fa-code icono icono2"></i>}
          </div>

          {source && (
            <div
              className="item subitem"
              onClick={() => setComponents(!components)}
            >
              <i className={arrowComponents}></i>
              <i
                className={
                  components
                    ? "fas fa-folder-open carpeta carpeta4"
                    : "fas fa-folder carpeta carpeta4"
                }
              ></i>
              <small>Components</small>
            </div>
          )}

          {components && (
            <div>
              <div
                className="item subitem2"
                onClick={() => addOpen("Perfil.jsx")}
              >
                <GrReactjs style={{ color: "#00BCD4", fontSize: "15px" }} />
                <small>Perfil.jsx</small>
              </div>
              <div
                className="item subitem2"
                onClick={() => addOpen("Experiencia.jsx")}
              >
                <GrReactjs style={{ color: "#00BCD4", fontSize: "15px" }} />
                <small>Experiencia.jsx</small>
              </div>
              <div
                className="item subitem2"
                onClick={() => addOpen("Experiencia_2.jsx")}
              >
                <GrReactjs style={{ color: "#00BCD4", fontSize: "15px" }} />
                <small>Experiencia_2.jsx</small>
              </div>
              <div
                className="item subitem2"
                onClick={() => addOpen("Estudios.jsx")}
              >
                <GrReactjs style={{ color: "#00BCD4", fontSize: "15px" }} />
                <small>Estudios.jsx</small>
              </div>
              <div
                className="item subitem2"
                onClick={() => addOpen("Certificados.jsx")}
              >
                <GrReactjs style={{ color: "#00BCD4", fontSize: "15px" }} />
                <small>Certificados.jsx</small>
              </div>
              <div
                className="item subitem2"
                onClick={() => addOpen("Github.jsx")}
              >
                <GrReactjs style={{ color: "#00BCD4", fontSize: "15px" }} />
                <small>Github.jsx</small>
              </div>
              <div className="item subitem2">
                <IoLogoJavascript
                  style={{ color: "#FFCA28", fontSize: "15px" }}
                />
                <small>App.js</small>
              </div>
              <div className="item subitem2">
                <IoLogoCss3
                  style={{ color: "rgb(41, 101, 241)", fontSize: "15px" }}
                />
                <small>App.css</small>
              </div>
            </div>
          )}
          <div className="item subitem">
            <GrNode style={{ color: "#8BC34A", fontSize: "15px" }} />
            <small>package.json</small>
          </div>
          <div className="item subitem">
            <GrNode style={{ color: "#8BC34A", fontSize: "15px" }} />
            <small>package-lock.json</small>
          </div>
          <div className="item subitem">
            <i className="fas fa-file-alt carpeta readme"></i>
            <small>README.md</small>
          </div>
        </section>
      )}
    </Main>
  );
};

export default Menu;
