import React from "react";
import styles from "./Styles";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import {
  VscDebugAlt2,
  VscSearch,
  VscExtensions,
  VscSourceControl,
  VscLiveShare,
} from "react-icons/vsc";

const Aside = ({ setCurrent, current }) => {
  const { AsideContenedor } = styles;
  const active = current ? "far fa-copy icon activo" : "far fa-copy icon";
  const popup = (e) => {
    e.preventDefault();
    const { value } = e.target;

    window.open(
      "https://github.com/Lismairy-Sanchez",
      "",
      "height=800, width=800"
    );
  };

  return (
    <AsideContenedor>
      <i className={active} onClick={() => setCurrent(!current)}></i>
      <ListItem>
        <ListItemIcon>
          <VscSearch style={{ color: "#5F7E97", fontSize: "25px" }} />
        </ListItemIcon>
      </ListItem>
      <ListItem>
        <ListItemIcon>
          <VscSourceControl
            style={{ color: "#5F7E97", fontSize: "25px", cursor: "pointer" }}
            onClick={popup}
          />
        </ListItemIcon>
      </ListItem>
      <ListItem>
        <ListItemIcon>
          <VscDebugAlt2 style={{ color: "#5F7E97", fontSize: "25px" }} />
        </ListItemIcon>
      </ListItem>
      <ListItem>
        <ListItemIcon>
          <VscExtensions style={{ color: "#5F7E97", fontSize: "25px" }} />
        </ListItemIcon>
      </ListItem>
      <ListItem>
        <ListItemIcon>
          <VscLiveShare style={{ color: "#5F7E97", fontSize: "25px" }} />
        </ListItemIcon>
      </ListItem>
    </AsideContenedor>
  );
};

export default Aside;
