import React, { useState } from "react";

import Perfil from "./Perfil/Perfil.jsx";
import Certificados from "./Certificados/Certificados.jsx";
import Estudios_Realizados from "./Estudios/Estudios";
import Experiencia_Laboral from "./Experiencia/Experiencia";
import Experiencia_Laboral2 from "./Experiencia/Experiencia_2";
import Github from "./Github/Github.jsx";
import Empty from "./Empty/Empty";
import Loader from "../Loader/Loader";
import styles from "./Styles";
import { GrReactjs } from "react-icons/gr";

const Section = ({ currentItem, open, setOpen, setCurrentItem }) => {
  const { Section } = styles;

  const [loader, setLoader] = useState(true);

  const removeItem = (name) => {
    const nuevo = open.filter((ele) => ele !== name);
    setOpen(nuevo);
    setCurrentItem(nuevo[nuevo.length - 1]);
  };

  return !loader ? (
    <Section className="hola">
      <div className="header-section">
        {open.map((ele, index) => (
          <div
            className={
              currentItem === ele ? "openItem currentItem" : "openItem"
            }
            key={index}
          >
            <div className="nombre" onClick={() => setCurrentItem(ele)}>
              <GrReactjs style={{ color: "#00BCD4", fontSize: "15px" }} />
              <small className="componente">{ele}</small>
            </div>
            <small
              className="cerrar"
              title="Close (Ctrl+F4)"
              onClick={() => removeItem(ele)}
            >
              x
            </small>
          </div>
        ))}
      </div>

      <div className="seccion-actual">
        {/* <Perfil /> */}
        {currentItem === "Perfil.jsx" && <Perfil />}
        {currentItem === "Experiencia.jsx" && <Experiencia_Laboral />}
        {currentItem === "Experiencia_2.jsx" && <Experiencia_Laboral2 />}
        {currentItem === "Estudios.jsx" && <Estudios_Realizados />}
        {currentItem === "Certificados.jsx" && <Certificados />}
        {currentItem === "Github.jsx" && <Github />}
        {!currentItem && <Empty />}
      </div>
    </Section>
  ) : (
    <Loader loader={setLoader} />
  );
};

export default Section;
